.location {
  &__address {
    $fs: 14;
    font-size: em($fs, $fs_base);
    line-height: em(26, $fs);
    letter-spacing: em(0.15, $fs);
    color: $primary;
    margin-bottom: em(35, $fs);
    @include media(mobile) {
      text-align: center;
      margin-bottom: em(10, $fs);
    }
  }
}